/* Fuentes */
@font-face {
  font-family: 'Delicious Handrawn';
  src: url('https://fonts.googleapis.com/css2?family=Delius+Swash+Caps&display=swap') format('truetype');
}

/* Colores */
:root {
  --color-primario: #5497fc;
  --color-secundario: #01579b;
  --color-fondo: #e1f5fe;
  --color-texto: #212121;
}


/* Estilos generales */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  -webkit-user-drag: none; /* para Safari */
  -khtml-user-drag: none; /* para Konqueror */
  -moz-user-drag: none; /* para Firefox */
  -o-user-drag: none; /* para Opera */
}


body {
  font-family: 'Delicious Handrawn';
  font-size: 1em;
  color: var(--color-texto);
  background: linear-gradient(to bottom right, var(--color-primario), var(--color-secundario));
}

nav {
  font-family: 'Delicious Handrawn';
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em 2em;
  border-radius: 0.5em;
}

nav a {
  color: var(--color-texto);
  text-decoration: none;
  font-size: 1.2em;
  font-weight: bold;
  transition: all 0.3s ease;
}

nav a:hover {
  transform: scale(1.1);
  color: var(--color-secundario);
}

.miel {
  width: 20%;
  max-width: 4em;
  height: 4em;
  margin-left: auto;
  margin-right: 1em;
  animation-name: rotate;
  animation-duration: 0.8s;
  animation-iteration-count: infinite;
}

@keyframes rotate {
  0% {
    transform: translateX(0);
  }
  20% {
    transform: translateX(-0.5em);
  }
  40% {
    transform: translateX(0.5em);
  }
  60% {
    transform: translateX(-0.5em);
  }
  80% {
    transform: translateX(0.5em);
  }
  100% {
    transform: translateX(0);
  }
}

.connect-wallet button {
  font-family: 'Delicious Handrawn';
  background: linear-gradient(to right, #e085f7, #7f53f7, #09edf5, #fd9086);
  color: white;
  height: 3.4em;
  border: none;
  border-radius: 0.75em; /* 12px */
  padding: 0.625em 2.5em; /* 10px 40px */
  font-size: 1em; /* 16px */
  transition: all 0.3s ease;
  margin: 0.3em 0.2em 0.2em 0.2em;
  background-size: 400% 400%;
  animation: gradient 8s ease infinite;
}

.connect-wallet button:hover {
  background-position: right center;
  cursor: pointer;
}

.connect-wallet button:focus {
  outline: none;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
/*balances */
.container {
