/* Animaciones */
.balance-contract:hover,
.rewards-contract:hover,
.hasInvested-contract:hover,
.bnbTvl-contract:hover,
.dailyReward-contract:hover {
  animation-name: pulse0;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes pulse0 {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
  /*Botones*/
  .button-container {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 2em;
    }
    
    .staking button,
    .claim button,
    .withdraw button {
      font-family: 'Delicious Handrawn';
      color: #fff;
      background-color: #fce46e;
      border: none;
      padding: 0.5em 1em;
      border-radius: 5px;
      font-size: 1.2em;
      margin: 0 1em;
      cursor: pointer;
      transition: all 0.3s ease-in-out;
    }
    
    .staking button:hover,
    .claim button:hover,
    .withdraw button:hover {
      background-color: #ffbe76;
      transform: scale(1.1);
    }
    
    .staking button:focus,
    .claim button:focus,
    .withdraw button:focus {
      outline: none;
    }
    
    .staking button:active,
    .claim button:active,
    .withdraw button:active {
      background-color: #ffbe76;
      transform: translateY(2px);
    }
    
    /* Animaciones */
    .staking button:hover,
    .claim button:hover,
    .withdraw button:hover {
      animation-name: pulse;
      animation-duration: 1s;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
    }
    
    @keyframes pulse {
      0% {
        transform: scale(1);
      }
      50% {
        transform: scale(1.2);
      }
      100% {
        transform: scale(1);
      }
    }

    
