@font-face {
    font-family: 'Delicious Handrawn';
    src: url('https://fonts.googleapis.com/css2?family=Delius+Swash+Caps&display=swap') format('truetype');
  }
  
 .menu {
    position: absolute;
    top: 0;
    left: 0;
  cursor: pointer;
  margin: 1em;
}

.menu-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3.5em;
    height: 3.5em;
    background-color: #FCA311;
    border-radius: 50%;
    box-shadow: 0px 0px 0.31em rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
  }
  

.menu-icon:hover {
  transform: scale(1.1);
  background-color: #FB923C;
}

.menus {
    max-width: 2.4em;
    height: 2.4em;
    margin:1em;
}

.menu-items {
  position: absolute;
  top: 3.125em;;
  left: 100%;
  list-style: none;
  background-color: #FCA311;
  border-radius: 0.625em;
  padding: 0.5em;
  box-shadow: 0px 0px 0.3125em rgba(0, 0, 0, 0.2);
  animation-name: slide-down;
  animation-duration: 0.3s;
  animation-fill-mode: both;
  z-index: 1;
}

.menu-items li {
  margin-bottom: 0.5em;
}

.menu-items a {
  text-decoration: none;
  color: white;
  transition: all 0.3s ease;
}

.menu-items a:hover {
  color: #c8ecfd;
}

@keyframes slide-down {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}